import React from 'react'
import {render, fireEvent, act} from '@testing-library/react-native'
import {Dice} from './'

jest.mock('react-native/Libraries/Animated/NativeAnimatedHelper')
jest.mock('./Animated', () => {
  return {
    ...jest.requireActual('./Animated'),
    createAnimatedComponent: (component: any) => {
      return component
    },
  }
})

describe('Dice Component', () => {
  const mockRandom = jest.fn()
  const mockUpdateStep = jest.fn()

  // Передайте моки через mockProps
  const mockProps = {
    count: 1,
    players: 2,
    disabled: false,
    canGo: true,
    isReported: false,
    updateStep: mockUpdateStep,
    random: mockRandom,
  }

  it('renders correctly with the provided props', () => {
    const {getByTestId} = render(<Dice {...mockProps} />)
    expect(getByTestId('dice-component')).toBeDefined()
  })

  it('calls the random function and updateStep on press when not disabled', () => {
    const {getByTestId} = render(<Dice {...mockProps} />)

    // Симулируйте нажатие на компонент, обернув его в act(...)
    act(() => {
      fireEvent.press(getByTestId('dice-component'))
    })

    // Проверьте, что функции random и updateStep были вызваны по одному разу
    expect(mockRandom).toHaveBeenCalledTimes(1)
    expect(mockUpdateStep).toHaveBeenCalledTimes(1)
  })
})
